import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import DogHistory from '../../components/dog/history';
import DogAddLog from '../../components/dog/addlog';
import DogCalendar from '../../components/dog/calendar';

const initialState = {
	isLoading: false,
};

@observer
class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadAll();
    }

	async loadAll() {
		this.setState({ isLoading: true });
		await this.loadFeed();
		this.setState({ isLoading: false });
    }

	async loadFeed(props = this.props) {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;

		const currentMonth = new Date().getMonth() + 1;
        const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;
        await userStore.getFeed({ month, year, team, dog: dog.id });
    }

	setHistoryType = (historyType) => {
		this.setState({ historyType });
    }

	setDay = (yyyy, mm, dd) => {
		this.setState({ yyyy, mm, dd });
	}

	resetDay = () => {
		this.setState({ yyyy: null, mm: null, dd: null });
	}

	// componentWillUnmount() {
	//     PubSub.unsubscribe(subscription);
	// }
    componentDidMount() {
        this.loadAll();
    }

	render() {
		const { scrolledDown } = this.props;
		const { isLoading, historyType, yyyy, mm, dd } = this.state;
        // const filterHistories = historyType ? (e) => e.historyType === historyType : () => true;
		const filterHistories = (e) => {
            let isOk = true;
            if (historyType) {
                isOk = e.historyType === historyType;
            }
            if (isOk && yyyy && mm && dd) {
                isOk = e.year === yyyy && e.month === mm && e.day === dd;
            }
            return isOk;
        }

		const { userStore, appState } = this.props.stores;
        const { darkmode } = appState;
		const { month, year } = this.props;
		const { historyFeed } = userStore;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top pt-2' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> <Text id='dogs.dashboard'>Oversikt</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/list' class='text-secondary stretched-link'><i class='fas fa-list' /> <Text id='dogs.list'>Liste</Text></a></nobr>
					</div>
					<div class='col text-center border-bottom border-primary subtopmenu'>
						<nobr><a href='/dogs/feed' class='text-primary stretched-link'><i class='fas fa-rss' /> <Text id='dogs.feed'>Feed</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu'>
						<nobr><a href='/dogs/todo' class='text-secondary stretched-link'><i class='fas fa-calendar-check' /> <Text id='dogs.todo'>Todo</Text></a></nobr>
					</div>
				</div>
					
				<div class='row'>
					<div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<span class='float-right'><DogAddLog stores={this.props.stores} dog={{}} /></span>
							<h5><Text id='feed.title'>Siste fra hundene</Text></h5>
						</div>

						<DogCalendar stores={this.props.stores} basehref={`/dogs/feed/`} month={month} year={year} callback={this.setHistoryType} setDay={this.setDay} />

						{!isLoading && historyFeed && historyFeed.length === 0 && (
							<div class='mb-5 py-3 text-center border-top border-bottom text-muted'>
								<div class='display-4'>
									<i class='fas fa-dog' />
								</div>
								<h5>
									<Text id='feed.no-log'>Ingen hendelser fra hundene.</Text>
								</h5>
								<p>
									<Text id='feed.tip-1'>Det er fint √• legge inn hendelser (l√∏petid, skader, vaksinering, vetrin√¶rbes√∏k, mm) og bilder av hundene i loggen.</Text>
								</p>
								<p>
									<Text id='feed.tip-2'>P√• denne m√•ten s√• vil hele teamet ditt se hva som skjer med hundene og hva som trengs √• f√∏lges opp.
									Dette er en slags <strike>Face</strike>Dogbook for hundene deres.</Text>
								</p>
								<br />
								<a class='btn btn-info mt-3' href='/dogs/'>
									<i class='fas fa-dog' /> <Text id='feed.add-log'>Legg til logg p√• hundene</Text>
								</a>
							</div>
						)}

						{yyyy && mm && dd && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 clearfix mb-3'>
								<button class='btn btn-sm btn-secondary float-right' onClick={this.resetDay}>
									<Text id='feed.show-all-days'>Vis alle</Text>
								</button>
								<h5>{yyyy}-{util.padDate(mm)}-{util.padDate(dd)}:</h5>
							</div>
						)}

						{historyFeed && historyFeed.filter(filterHistories).map(line => (
							<DogHistory stores={this.props.stores} line={line} />
						))}
					</div>
				</div>
			</div>
		);
	}
}

export default Home;
